import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from '../store/index.js';

Vue.use(VueRouter)
//En este archivo se cargan todas las rutas de la aplicacion, cuando se accede a una vista de la carpeta /views se se carga el componente y se renderiza la vista
//Router es el encargado de administrar las rutas de la aplicacion y toda la configuracion irá en este archivo

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '*',
    name: 'error',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Error404.vue')
  },
  {
    path: "/login",
    name: "login",
    component: () =>
        import(/* webpackChunkName: "about" */ "../views/Login.vue"),
  },
  {
    path: "/register",
    name: "register",
    component: () =>
        import(/* webpackChunkName: "about" */ "../views/Register.vue"),
  },
  {
    path: "/categoryNews",
    name: "categoryNews",
    component: () =>
        import(/* webpackChunkName: "about" */ "../views/CategoryNews.vue"),
  },
  {
    path: "/user/profile",
    name: "userProfile",
    component: () =>
        import(/* webpackChunkName: "about" */ "../views/PanelUser.vue"),
    meta: { requireAuth:true },
    children:[
      {
        path: "calendar",
        component: () =>
            import(/* webpackChunkName: "about" */ "../components/Calendar"),
      },
      {
        path: "date",
        component: () =>
            import(/* webpackChunkName: "about" */ "../components/DateDoctor"),
      },
      {
        path: "informes",
        component: () =>
            import(/* webpackChunkName: "about" */ "../components/ListaInformes"),
      }
    ]
  },
  {
    path: "/doctor/profile",
    name: "doctorProfile",
    component: () =>
        import(/* webpackChunkName: "about" */ "../views/PanelDoctor.vue"),
    meta: { requireAuth:true },
    children:[
      {
        path: "calendar",
        component: () =>
            import(/* webpackChunkName: "about" */ "../components/Calendar"),
      },
      {
        path: "informe",
        component: () =>
            import(/* webpackChunkName: "about" */ "../components/InformeDoctor"),
      },
      {
        path: "pacientes",
        component: () =>
            import(/* webpackChunkName: "about" */ "../components/ListaPacientes"),
      },
      {
        path: "citasPacientes",
        component: () =>
            import(/* webpackChunkName: "about" */ "../components/ListaCitasPacientes"),
      },
    ]
  },
  {
    path: "/news/:similarNew",
    name: "news",
    component: () =>
        import(/* webpackChunkName: "about" */ "../views/News.vue"),
  },
  {
    path: "/covid",
    name: "covid",
    component: () =>
        import(/* webpackChunkName: "about" */ "../views/Covid.vue"),
  },
  {
    path: "/contact",
    name: "contact",
    component: () =>
        import(/* webpackChunkName: "about" */ "../views/Contact.vue"),
  },
  {
    path: "/aboutUs",
    name: "aboutUs",
    component: () =>
        import(/* webpackChunkName: "about" */ "../views/AboutUs.vue"),
  },
]

//Con el modo history hacemos que las rutas dentro de la aplicacion sean reales, de lo contrario apareceria un # al final de la url indicando que son solo rutas de la app
// por lo que si se accede a una url directamente desde otra pagina daría un error
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

//Esta funcion se ejecuta antes de ir a cualquier ruta
router.beforeEach((to,from,next) => {
  const rutaProtegida = to.matched.some(record => record.meta.requireAuth);
  //Si la ruta esta protegida y no existe el token, es decir no ha iniciado sesion, se redirige al usuario al formulario de login
  if(rutaProtegida && store.state.token === ''){
    next({name:'login'});
  }else{
    next();
  }
})

export default router
