import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import axios from "axios";
import VueAxios from "vue-axios";

//ESTE ES EL ARCHIVO PRINCIPAL DE LA APP Y DONDE SE CARGAN TODOS LOS MODULOS QUE LUEGO SE USAN DENTRO DE LA APP Y DONDE SE RENDERIZA EL COMPONENTE BASE APP.VUE

Vue.use(VueAxios, axios);
// Agregamos la URL base de nuestra API para que cada vez que llamemos a AXIOS no haya que poner /api
//axios.defaults.baseURL = "http://localhost:8080/api";
axios.defaults.baseURL = "https://vallasalud.herokuapp.com/api";
import bootstrap from 'bootstrap'
import BootstrapVue from "bootstrap-vue";



//PRELOADER para la carga (circulo de carga) cuando aun no están listos los datos
import loader from "vue-ui-preloader";
Vue.use(loader);



//GRAFICOS para la tabla de COVID

import VueApexCharts from 'vue-apexcharts'
Vue.use(VueApexCharts)

Vue.component('apexchart', VueApexCharts)


//Componente CALENDARIO que se usa en el perfil de los usuarios
import VCalendar from 'v-calendar'

Vue.use(VCalendar);


import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
Vue.use(BootstrapVue);
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
