import Vue from 'vue'
import Vuex from 'vuex'

//Para decodificar el jwt
import decode from 'jwt-decode'

import router from '../router/index';

Vue.use(Vuex)

//La Store es un modulo de VUE, contiene todo el estado de la aplicacion y todos los datos que se pueden acceder globalmente desde cualquier componente
//Ademas todos los datos estan centralizados y nos evitamos un problema bastante comun e importante que es pasar datos de componentes a subcomponentes (anidados)
//y se volveria bastante tedioso e insostenible

export default new Vuex.Store({
  //Variables globales
  state: {
    token: localStorage.getItem('userToken') || '',
    usuario: '',
    userInfo: '',
    isDoctor: false
  },
  mutations: {
    //El payload es el token
    //Metodo que obtiene el usuario a traves del token que se envia desde back con toda la información
    obtenerUsuario(state,payload){
      state.token = payload;
      if(payload === ''){
        state.usuario = '';
      }else{
        state.usuario = decode(payload)
      }
    },
    guardarInfo(state,user){
      state.userInfo = user;
    },
    isADoctor(state,isDoctor){
      state.isDoctor = isDoctor;
    }
  },
  actions: {
    infoUser({commit},user){
      commit('guardarInfo',user);
    },
    checkDoctor({commit},isDoctor){
      commit('isADoctor',isDoctor);
    },
    guardarUsuario({commit},payload){
      //Si se envia el token del usuario se guarda en Local Storage para hacer la sesion persistente
      localStorage.setItem('userToken',payload);
      commit('obtenerUsuario',payload);
    },
    cerrarSesion({commit}){
      //Metodo que borra el token de Local Storage
      commit('obtenerUsuario','');
      localStorage.removeItem('userToken');
      router.push({name:'login'});
    },
    leerToken({commit}){
      //Metodo que lee si hay un token o no y llama a obtenerUsuario
      const token = localStorage.getItem('userToken');
      if(token){
        commit('obtenerUsuario',token);
      }else{
        commit('obtenerUsuario','');
      }

    }
  },
  getters:{
    //Getters para obtener determinada informacion de la Store
    isLogged: state => !!state.token,
    userId: state => state.usuario.uid,
    userInfo: state => state.userInfo,
    isDoctor:state => state.isDoctor
  },
  modules: {
  }
})
